.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
.fixed-navbar {
    background: white;
    box-shadow: 0 0 3px rgba(black, .3);
    z-index: 999;
}

.navbar-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 60px;
    padding: 0 50px;
    top: 0;
    left: 0;
    right: 0;
    transition: .3s ease;
    position: fixed;
}

.navbar-wrapper .name {
    font-weight: 600;
    font-size: large;
}

.navbar-wrapper .links-wrapper a {
    text-decoration: none;
    color: black;
    padding: 10px;
    font-family: "Open Sans";
    opacity: 0.6;
    transition: all 0.2s ease-in-out;
    font-size: 14px;
    background-color: transparent;
    outline: none;
    border: 0px;
}

.navbar-wrapper .links-wrapper a:hover {
    opacity: 1;
}

.navbar-wrapper .links-wrapper .active {
    color: black;
}
.header-container {
    display: flex;
    justify-content: center;
    height: 100vh;
    background-image: url(/myPortfolio/static/media/bg1.cb292953.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

.header-wrapper {
    width: 80vw;
    margin-top: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header-wrapper h2 {
    font-size: 1rem;
    text-align: center;
}

.heading-wrapper h1 {
    font-size: 4rem;
    text-align: center;
    line-height: 28px;
}

p {
    width: 50%;
    text-align: center;
    overflow: hidden;
    margin: 3px 0;
    font-size: 20px;
}

a {
    text-decoration: none;
    color: white;
}

.primary-btn {
    margin: 10px;
    padding: 10px;
    background-color: black;
    color: white;
    border: 0px;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    cursor: pointer;
    width: 110px;
    margin-top: 35px;
}

.primary-btn:hover {
    transform: scale(1.1);
}

@media screen and (min-width:768px) and (max-width: 1023px) {
    .header-wrapper h2 {
        font-size: 16px;
    }
    .heading-wrapper h1 {
        font-size: 2.5rem;
    }
    p {
        font-size: 14px;
        width: 100%;
    }
    .header-container {
        height: -webkit-max-content;
        height: max-content;
        padding-bottom: 100px;
    }
}

@media screen and (max-width:767px) {
    .header-wrapper h2 {
        font-size: 14px;
    }
    .heading-wrapper h1 {
        font-size: 1.5rem;
        line-height: 15px;
    }
    p {
        font-size: 12px;
        width: 100%;
    }
    .header-container {
        height: -webkit-max-content;
        height: max-content;
        padding-bottom: 100px;
    }
}
